import { setStore, getStore, removeStore, Trim, getSStore, getBirthDay,setSStore } from "@/utils/util"
import Load from '@/components/Load.vue'
import { getOrderParams, addPassengerBirthDay } from './rules'
export default {
	data() {
		return {			
			loading:false,			
			//证件类型
			certificates: [
				{ value: '1', label: '身份证' },
				{ value: '2', label: '护照' }
			],
			data: null, //航班信息
			index: null, //prices索引
			checkPrice: null, //验价后的实际价格信息
			//联系人
			contact: { 
				contactUser: '石松岩',
				contactMobile: '15516507061'
			},
			//乘机人
			passengerList: [{
				birthDate: "",
				lastName_cn: '石松岩',
				docId: '410423199209189056',
				passageType: 1,
				mobile: '15516507061',
				docType: '1'
			}], 
		}
	},
	computed: {

	},
	watch: {

	},
	methods: {
		//添加乘机人
		add() {
			this.passengerList.push({
				birthDate: "",
				lastName_cn: '',
				docId: '',
				passageType: 1,
				mobile: '',
				docType: '1'
			});
		},
		//删除乘机人
		del(index) {
			if(this.passengerList.length > 1) {
				this.passengerList.splice(index, 1);
			}
		},
		//支付
		pay() {
			
			let passengerList = addPassengerBirthDay(this.passengerList);
			let params = getOrderParams(this.data ,this.checkPrice, this.index,this.contact,passengerList);
			if(!params.result){
				this.$message.error(params.msg);	
				return;
			}
			this.loading = true;
			this.$http.post('/order/create',params.data)
			.then((res) => {
				this.loading = false;
				let success = res.data.success;
				if(success){
					setSStore("flight_orderNo",res.data.obj)
					setSStore("flight_passengers",this.passengerList)
					this.$message.success("下单成功");		
					this.$router.push({path:'/flight/pay',query:{type:'flight'}})
					return 
				}
				this.$notify.error({
                    title: '下单失败',
                    message: res.data.msg
                });
			})

		},
		
		//飞行时长
		duration(data) {
			var duration = 0;
			for(var i = 0; i < data.length; i++) {
				duration += data[i].flightDuration;
			}
			return duration;
		},
	},
	components: {
		Load
	},
	created() {
		this.data = JSON.parse(getSStore('flight_shopping')).data;
		this.index = JSON.parse(getSStore('flight_shopping')).index;
		this.checkPrice = JSON.parse(getSStore('flight_orderConfirm'));
	}
}


